@import"https://fonts.googleapis.com/css2?family=Roboto&family=Source+Code+Pro&display=swap";*{margin:0;padding:0}body{transition:.3s ease-in;background-color:ivory;color:#202020}body:has(#dark){background-color:#202020;color:ivory}#root{width:100vw;height:100vh;margin:0;padding:0;overflow-x:hidden}hr{border:1px solid #202020}#dark hr{border-color:ivory}.main{font-family:"Roboto",sans-serif;margin-inline:auto;display:flex;justify-content:center;width:100vw;height:100vh}.board{width:min(1000px,93vw);text-align:center}.board .leaderboard{margin-top:3rem;margin-bottom:1rem}.board .duration{margin-top:1.25rem;display:flex;justify-content:center;gap:1rem}.board .duration>button{color:inherit;border:2px solid ivory;padding:.75rem 2rem;border-radius:50px;background-color:rgba(0,0,0,0);cursor:pointer;transition:.2s ease-in}#light .board .duration>button{border:2px solid #202020}.board .duration>button:hover{background-color:ivory;color:#202020}.board .duration>button.selected{background-color:ivory;color:#202020}.board .duration>button.selected:hover{opacity:.5}#light .board .duration>button:hover,#light .board .duration>button.selected{background-color:#202020;color:ivory}.profile{width:100%;margin-top:3rem}.profile .flex{width:100%;border-radius:1rem;display:flex;padding:.5rem 0;justify-content:space-between;gap:1rem;text-align:left;margin-bottom:1rem;box-shadow:1px 4px 22px -10px rgba(0,0,0,.2);border:2px solid rgba(211,211,211,.4)}.profile .flex:nth-child(1){position:relative;background-color:rgba(212,175,55,.58)}.profile .flex:nth-child(1)::before{content:"üèÜ";position:absolute;font-size:74px;margin-left:-100px;margin-top:-16px}.profile .flex:nth-child(2){background-color:rgba(192,192,192,.58)}.profile .flex:nth-child(3){background-color:rgba(205,127,50,.58)}.profile .flex .item,.profile .flex .item>*{display:flex;align-items:center}.profile .flex .score{margin-right:1rem;font-family:"Source Code Pro",monospace}.profile .flex img{width:80px;height:80px;border-radius:50%;margin-left:.5rem}.profile .flex .info{padding:1rem}.profile .flex .info .name{font-size:1.25rem;font-weight:600}@media only screen and (max-width: 1190px){.profile .flex:nth-child(1)::before{content:"üèÜ";position:absolute;font-size:2.5rem;transform:translate(80px, -20px)}}.themeSwitch{display:grid;place-self:center;position:fixed;background-color:#fff;border-radius:100%;cursor:pointer;border:none;top:1rem;right:1rem;height:50px;width:50px;transition:.3s ease-in}#dark .themeSwitch{transform:rotate(180deg)}@media only screen and (max-width: 768px){.board{margin-bottom:3rem}.themeSwitch{height:30px;width:30px}}